<template>
	<div id="seckill-detail">
		<public-header :title="title"></public-header>
		<div class="content">
			<van-swipe :autoplay="3000" v-if="goods.slides && goods.slides.length > 0">
				<van-swipe-item v-for="(slide, index) in goods.slides" :key="index">
					<img :src="slide" alt="">
				</van-swipe-item>
			</van-swipe>
			<div class="goods-thumb" v-else>
				<img :src="goods.thumb" alt="">
			</div>
			<div class="decoration">
				<div class="price-container">
					<div class="price">¥<span>{{goods.price}}</span></div>
					<div class="old-price"> 原价¥{{goods.old_price}} </div>
				</div>
				<div class="countdown" v-if="goods.end == 0 && goods.all_sailed == 0">
					<div class="countdown_text">距离结束还有</div>
					<div class="time">
						<span>{{time.days}}</span>
						:
						<span>{{time.hours}}</span>
						:
						<span>{{time.minutes}}</span>
						:
						<span>{{time.seconds}}</span>
					</div>
				</div>
				<div class="end-tip" v-else>{{goods.end ? '活动已结束' : '商品已售罄'}}</div>
			</div>
			<div class="activity-info">
				<div class="title-favor van-hairline--bottom">
					<div class="title">{{goods.title}}</div>
					<div class="favor" v-if="goods.click > 0"><van-icon name='hotfill'></van-icon>{{goods.click}}人关注</div>
				</div>
				<div class="title-favor van-hairline--bottom">
					<div class="title">抢购时间</div>
					<div class="favor goods-time">{{goods.starttime_cn}} 至 {{goods.endtime_cn}}</div>
				</div>
				<div class="sold" v-if="buyers_num > 0">
					<div class="avatars">
						<div class="avatar-item" v-for="(item,index) in buyers" :key="index">
							<img :src="item.avatar" alt="">
						</div>
					</div>
					<div>已有{{buyers_num}}人购买<van-icon name='right'></van-icon></div>
				</div>
			</div>
			<div class="info">
				<div class="info-title van-hairline--bottom">适用店铺</div>
				<div class="info-content">
					<div class="store van-hairline--bottom">
						<div class="logo">
							<img :src="store.logo" alt="">
						</div>
						<div class="store-info">
							<div class="title">{{store.title}}</div>
							<div class="opening-hours"> 营业时间 {{store.business_hours_cn}} </div>
						</div>
						<a class="tel" :href="'tel:' + store.telephone"><van-icon name='telephone'></van-icon></a>
					</div>
					<div class="address" @click="util.jsLocation({lat: store.location_x, lng: store.location_y});">
						<van-icon name='locationfill'></van-icon>
						<div class="address-info">{{store.address}}</div>
						<van-icon name='right'></van-icon>
					</div>
				</div>
			</div>
			<div class="info steps">
				<div class="info-title van-hairline--bottom">抢购流程</div>
				<div class="info-content">
					<div class="step">
						<div class="icon icon-friend"></div>
						<div class="step-title">参与抢购</div>
					</div>
					<div class="line"></div>
					<div class="step">
						<div class="icon icon-squarecheck"></div>
						<div class="step-title">付款成功</div>
					</div>
					<div class="line"></div>
					<div class="step">
						<div class="icon icon-edit1"></div>
						<div class="step-title">到店验证</div>
					</div>
				</div>
			</div>
			<div class="info tips">
				<div class="info-title van-hairline--bottom">购买须知</div>
				<div class="info-content">
					<div class="tip-item">
						<span class="tip-title">活动截止日期：</span>
						<span>{{goods.endtime_cn}}</span>
					</div>
					<div class="tip-item">
						<span class="tip-title">消费截止日期：</span>
						<span>购买后{{goods.use_limit_day}}天</span>
					</div>
				</div>
			</div>
			<div class="info-goods">
				<div class="info-title van-hairline--bottom">商品详情</div>
				<div class="info-content" v-html="goods.description"></div>
			</div>
			<div class="bottom-navs">
				<div class="navs van-hairline--top">
					<div class="nav-item" @click="util.jsUrl(store.url)">
						<van-icon name='shop'></van-icon>
						<div>店铺</div>
					</div>
					<a class="nav-item" :href="'tel:' + store.telephone">
						<van-icon name='telephone'></van-icon>
						<div>电话</div>
					</a>
					<router-link class="nav-item" :to="util.getUrl({path: '/pages/seckill/orderList'})">
						<van-icon name='order'></van-icon>
						<div>订单</div>
					</router-link>
				</div>
				<router-link class="buy-btn" :to="util.getUrl({path: '/pages/seckill/create', query: {id: id}})" v-if="goods.end == 0 && goods.all_sailed == 0">立即抢购</router-link>
				<div class="buy-btn disabled" v-else>{{goods.end ? '活动已结束' : '商品已售罄'}}</div>
			</div>
		</div>
		<transition name="loading">
			<iloading v-if="showPreLoading"></iloading>
		</transition>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	export default {
		components: {
			PublicHeader,
		},
		data() {
			return {
				title: '商品详情',
				showPreLoading: true,
				id: Number,
				goods: {},
				store: {},
				buyers: [],
				buyers_num: Number,
				time: {},
				flag: false
			}
		},
		methods: {
			onLoad() {
				this.util.request({
					url: 'seckill/seckill/detail',
					data: {
						id: this.id
					},
				}).then((res) => {
					this.showPreLoading = false;
					let result = res.data.message;
					if(result.errno) {
						this.util.$toast(result.message);
						return false;
					}
					this.goods = result.message.goods;
					this.store = result.message.store;
					this.buyers = result.message.buyers;
					this.buyers_num = result.message.buyers_num;
					this.title = this.goods.title;
					this.util.setWXTitle(this.title);
					if(this.goods.end == 0) {
						let time = setInterval(()=>{
							if(this.flag == true){
								clearInterval(time)
							}
							this.timeDown()
						},1000)
					}
				})
			},

			timeDown () {
				const endTime = new Date(this.goods.endtime_js);
				const nowTime = new Date();
				let leftTime = parseInt((endTime.getTime()-nowTime.getTime())/1000)
				let d = this.formate(parseInt(leftTime/(24*60*60)))
				let h = this.formate(parseInt(leftTime/(60*60)%24))
				let m = this.formate(parseInt(leftTime/60%60))
				let s = this.formate(parseInt(leftTime%60))
				if(leftTime <= 0){
					this.flag = true
				}
				this.time = {
					days: d,
					hours: h,
					minutes: m,
					seconds: s,
				}
			},

			formate (time) {
				if(time>=10){
					return time
				}else{
					return `0${time}`
				}
			}

		},
		created() {
			if(this.$route.query.id) {
				this.id = this.$route.query.id;
			}
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#seckill-detail .content{
		position: fixed;
		top: 46px;
		right: 0;
		left: 0;
		bottom: 50px;
		width: 100%;
		background: #f5f5f5;
		overflow-x: hidden;
		overflow-y: scroll;
	}
	#seckill-detail .content::-webkit-scrollbar {
		display: none;
	}
	#seckill-detail .content .van-swipe img, #seckill-detail .content .goods-thumb img{
		width: 100%;
		height: 200px;
		font-size: 0;
	}
	#seckill-detail .content .van-swipe .van-swipe-item, #seckill-detail .content .goods-thumb{
		font-size: 0;
	}
	#seckill-detail .content .decoration{
		height:50px;
		box-sizing:border-box;
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		background:linear-gradient(to right,#ff2d4b,#fff);
		color:#fff;
	}
	#seckill-detail .content .decoration .price-container{
		padding:5px 10px;
		display:flex;
		flex-flow:column nowrap;
		justify-content:space-between;
		line-height:1;
	}
	#seckill-detail .content .decoration .price-container .price span{
		font-size: 20px;
	}
	#seckill-detail .content .decoration .price-container .old-price{
		text-decoration:line-through;
		font-size:12px;
	}
	#seckill-detail .content .decoration .countdown{
		text-align:center;
		background-color: rgba(255, 255, 255, 0.2);
		display:flex;
		flex-flow:column nowrap;
		justify-content:space-between;
		padding:5px;
	}
	#seckill-detail .content .decoration .countdown .countdown_text{
		color:#666;
		font-size:12px;
	}
	#seckill-detail .content .decoration .countdown .time{
		color: #ff2d4b;
	}
	#seckill-detail .content .decoration .countdown .time span{
		display:inline-block;
		padding:2px 3px;
		background-color: #ff2d4b;
		color: #fff;
		border-radius:2px;
		font-size: 13px;
		min-width: 20px;
	}
	#seckill-detail .content .decoration .end-tip{
		color: #333;
		line-height: 50px;
		padding-right: 10px;
		font-size: 14px;
	}
	#seckill-detail .content .activity-info{
		background-color:#fff;
		padding:0 10px;
	}
	#seckill-detail .content .activity-info .title-favor{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		padding:10px 0;
	}
	#seckill-detail .content .activity-info .title-favor .title{
		max-width:70%;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
		font-size:14px;
		line-height: 24px;
	}
	#seckill-detail .content .activity-info .title-favor .favor{
		color:#666;
		font-size:13px;
	}
	#seckill-detail .content .activity-info .title-favor .favor .van-icon-hotfill{
		color:#ff2d4b;
		font-size:16px;
	}
	#seckill-detail .content .activity-info .title-favor .favor.goods-time{
		color: #999;
	}
	#seckill-detail .content .activity-info .sold{
		padding:10px 0;
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		color:#666;
	}
	#seckill-detail .content .activity-info .sold .avatars{
		position:relative;
		height:40px;
		width:60%;
	}
	#seckill-detail .content .activity-info .sold .avatar-item{
		width:40px;
		height:40px;
		box-sizing:border-box;
		border-radius:50%;
		border:2px solid #fff;
		position:absolute;
	}
	#seckill-detail .content .activity-info .sold .avatar-item:nth-child(2){
		z-index:2;
		left:30px;
	}
	#seckill-detail .content .activity-info .sold .avatar-item:nth-child(3){
		z-index:3;
		left:60px;
	}
	#seckill-detail .content .activity-info .sold .avatar-item:nth-child(4){
		z-index:4;
		left:90px;
	}
	#seckill-detail .content .activity-info .sold .avatar-item:nth-child(5){
		z-index:5;
		left:120px;
	}
	#seckill-detail .content .activity-info .sold .avatar-item:nth-child(6){
		z-index:6;
		left:150px;
	}
	#seckill-detail .content .activity-info .sold .avatar-item img{
		width:100%;
		height:100%;
		border-radius:100px;
	}
	#seckill-detail .content .activity-info .sold div{
		font-size: 13px;
		color: #666;
	}
	#seckill-detail .content .activity-info .sold div .van-icon-right{
		font-size: 14px;
	}
	#seckill-detail .content .info{
		background-color:#fff;
		margin-top:10px;
		padding:0 10px;
	}
	#seckill-detail .content .info .info-title{
		width:100%;
		padding:10px 0;
		font-size:14px;
		color:#333;
		line-height: 24px;
	}
	#seckill-detail .content .info .info-content{
		width:100%;
		box-sizing:border-box;
	}
	#seckill-detail .content .info .info-content .store{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		padding:10px 0;
	}
	#seckill-detail .content .info .info-content .store .logo{
		width: 40px;
		height: 40px;
	}
	#seckill-detail .content .info .info-content .store .logo img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
		border: none;
	}
	#seckill-detail .content .info .info-content .store .store-info{
		width:75%;
		font-size:14px;
		line-height:1;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
	}
	#seckill-detail .content .info .info-content .store .store-info .opening-hours{
		color:#999;
		margin-top:10px;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
	}
	#seckill-detail .content .info .info-content .store .tel .van-icon{
		color:#ff2d4b;
		font-size:18px;
	}
	#seckill-detail .content .info .info-content .address{
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
		padding:10px 0;
		line-height: 32px;
	}
	#seckill-detail .content .info .info-content .address .van-icon-locationfill{
		font-size:20px;
	}
	#seckill-detail .content .info .info-content .address .address-info{
		width:87%;
		overflow:hidden;
		text-overflow:ellipsis;
		white-space:nowrap;
		font-size:14px;
	}
	#seckill-detail .content .info .info-content .address .van-icon-right{
		color: #ccc;
		font-size: 14px;
	}
	#seckill-detail .content .info-goods{
		background-color:#fff;
		margin-top:10px;
		padding:0 10px;
	}
	#seckill-detail .content .info-goods .info-title{
		width:100%;
		padding:10px 0;
		font-size:15px;
		color:#333;
		line-height: 24px;
	}
	#seckill-detail .content .info-goods .info-content{
		width: 100%;
		font-size: 14px;
		padding: 10px 0;
	}
	#seckill-detail .content .bottom-navs{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 50px;
		background: #fff;
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
	}
	#seckill-detail .content .bottom-navs .navs{
		width:60%;
		display:flex;
		flex-flow:row nowrap;
		justify-content:space-between;
		align-items:center;
	}
	#seckill-detail .content .bottom-navs .navs .nav-item{
		display: block;
		width:33.33333%;
		height:50px;
		box-sizing:border-box;
		line-height:1;
		padding:7px 0;
		text-align:center;
		display:flex;
