	}
	#seckill-detail .content .bottom-navs .navs .nav-item div{
		font-size: 13px;
	}
	#seckill-detail .content .bottom-navs .buy-btn{
		display: block;
		width:40%;
		height:50px;
		line-height:50px;
		background-color:#ff2d4b;
		color:#fff;
		text-align:center;
		font-size:16px;
	}
	#seckill-detail .content .bottom-navs .buy-btn.disabled{
