		color: #fff;
	}

	/*抢购流程*/
	#seckill-detail .content .info.steps .info-content{
		display: flex;
		flex-flow: row nowrap;
		align-items: flex-start;
		justify-content: center;
		padding: 20px 0;
	}
	#seckill-detail .content .info.steps .info-content .step{
		text-align: center;
	}
	#seckill-detail .content .info.steps .info-content .step .icon{
		width: 50px;
		height: 50px;
		border-radius: 50%;
		text-align: center;
		line-height: 50px;
