		line-height: 25px;
		font-size: 13px;
	}
	#seckill-detail .content .info.tips .tip-item:before{
		position: absolute;
		content: '';
		width: 5px;
		height: 5px;
		border-radius: 5px;
		background-color: #D5D5D5;
		top: 50%;
		left: 0;
		margin-top: -2.5px;

	}
	#seckill-detail .content .info.tips .tip-item .tip-title{
		color: #ff2d4b;
	}
</style>


